.login-page {
  position: relative;
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
}

.login-form {
  display: flex;
  width: 400px;
  height: 400px;
  padding: 20px;
  box-shadow: 0 0 8px -4px #000;
  cursor: pointer;
  justify-content: center;
  flex-direction: column;
  z-index: 10;
  
}
.foot-bg{
  position: absolute;
  bottom: 0;
  width: 100%;
}